<template>
  <div class="sign">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>我的乐豆</span>
    </header>
    <div class="sign_package">
        <div class="sign_wrapper ">
            <section class="sign_content">
                <p class="sign_title">
                  <span>乐豆</span>
                  <strong>{{gash.yilingqu}}</strong>
                </p>
              <p class="line"></p>
              <p class="sign_btn">
                <span>每天可领取{{gash.day_number}}个,还剩{{gash.weilingqu}}乐豆可领取</span>
                <a href="javascript:void(0)" @click="getGash">领取乐豆</a>
              </p>
            </section>
        </div>
    </div>
    <p class="inter"></p>
    <div class="sign_record">
      <p>
        <span class="record">乐豆明细</span>
      </p>
      <ul  class="record_list" v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
        <li v-for="i in list">
          <p class="time">{{i.create_time | timeFilter}}</p>
          <p class="num">{{i.number}}个</p>
        </li>
        <li class="more_loading" v-if="!queryLoading && list.length !== 0">
          <mt-spinner type="fading-circle"  :size="25" v-show="moreLoading&&!allLoaded"></mt-spinner>
          <span v-show="allLoaded">已全部加载</span>
        </li>
      </ul>
      <!--<p class="record_foot">没有更多了~</p>-->
    </div>
  </div>
</template>
<script>
  import { Toast,InfiniteScroll, Indicator} from 'mint-ui';
  export default {
    data(){
      return {
        gash: {},
        list: [],
        moreLoading: false,
        queryLoading: false,
        allLoaded: false,
        pageSize: 20,
        pageNum: 1,
        totalNum: 10,
      }
    },
    filters:{
      timeFilter(val){
        return new Date(val*1000).toLocaleDateString();
      }
    },
    methods:{
      loadMore(){
        if(this.allLoaded){
          this.moreLoading = true;
          return;
        }
        if(this.queryLoading){
          return;
        }
        this.moreLoading = !this.queryLoading;
        this.pageNum++;
        this.getRecord(this.pageNum);
      },
      //领取乐豆
      getGash(){
        Indicator.open();
        this.post('home/User/lingquLd', {}).then(res => {
          Indicator.close();
          if (res.data.code === 10000) {
            Toast({
              message: res.data.info,
              duration: 1000
            });
            this.getInfo();
            this.getRecord();
          } else {
            Toast({
              message: res.data.info,
              duration: 1000
            });
          }
        })
      },
      getInfo(){
        this.post('home/User/getLegouNumber', {}).then(res => {
          if (res.data.code === 10000) {
              this.gash = res.data.info;
          }
        })
      },
      getRecord(page = 1){
        this.post('home/User/leDouLog',{
          page: page,
          number: 20
        }).then(res => {
            if (res.data.code === 10000) {
              this.list = [];
              this.totalNum = res.data.info.number;
              this.list = this.list.concat(res.data.info.data);
              this.allLoaded = this.list.length === this.totalNum;
              this.moreLoading = this.allLoaded;
            }
        })
      },
      back(){
        this.$router.go(-1);
      }
    },
    mounted(){
      this.getInfo();
      this.getRecord();
    }
  }
</script>
<style scoped>
  .header {
    width: 100%;
    height: 0.88rem;
    display: flex;
    align-items: center;
  }
  .count_top {
    background-color: #FFFFFF;
    justify-content: space-around;
  }
  .count_top img {
    width: 0.2rem;
    margin-left: 0.4rem;
  }
  .count_top span {
    margin-right: 0.4rem;
    flex: 1;
    font-size: 0.3rem;
    color: #111111;
    text-align: center;
  }
  .sign_package {
    height: 3rem;
    padding: 0.4rem 0.4rem 0 0.4rem;
    background-color: #423f3c;
  }
  .sign_wrapper {
    height: 2.6rem;
    background: linear-gradient(to bottom, #ff9000, #ff6f00);
    border-radius: 0.1rem;
  }
  .sign_content {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    padding-top: 0.4rem;
  }
  .sign_title span,strong{
    color: #111111;
    font-size: .2rem;
  }
  .sign_title strong {
    font-size: 0.6rem;
    font-weight: normal;
  }
  .line {
    height: 1px;
    background-color: #A04006;
    margin: 0.3rem;
  }
  .sign_btn {
    text-align: left;
    position: relative;
  }
  .sign_btn span {
    font-size: 0.24rem;
    color: #111111;
    margin-left: 0.2rem;
  }
  .sign_btn a {
    display: inline-block;
    color: #FFFFFF;
    font-size: 0.28rem;
    width: 1.6rem;
    height: 0.48rem;
    border-radius: 0.2rem;
    background-color: #922E0A;
    text-align: center;
    line-height: 0.48rem;
    position: absolute;
    right: 0.2rem;
    bottom: -0.05rem;
  }
  .inter {
    height: 0.2rem;
    background-color: #F2F2F2;
  }
  .sign_record {
    padding: 0.4rem 0.4rem 0 0.4rem;
    height: calc(100vh - 4.5rem);
    overflow-y: auto;
    text-align: center;
  }
  .record {
    font-size: 0.3rem;
    color: #cf9f82;
  }
  .record:before,.record:after{
    display: inline-block;
    content: '';
    width: 0.6rem;
    height: 1px;
    background-color: #cf9f82;
    vertical-align: middle;
    margin: 0 0.2rem;
  }
  .record_list{
    padding: 0.3rem 0;
  }
  .record_list li {
    padding: 0.3rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
  }
  .time {
    font-size: 0.28rem;
    color: #666666;
  }
  .num {
    color: #ff6f00;
    font-size: 0.34rem;
  }
  .record_foot {
    font-size: 0.24rem;
    color: #666666;
  }
  .record_list .more_loading {
    display: flex;
    justify-content: center;
  }
</style>



